import React, { Component } from 'react';
import { high } from 'nc-lightapp-front';
const { Refer } = high;

export default function(props = {}) {
	var conf = {
		multiLang: {
			domainName: 'uapbd/scmbase',
			currentLocale: 'zh-CN',
			moduleId: 'refer'
		},
		refType: 'grid',
		refName: 'REFER-000000' /* 国际化处理： 质量等级*/,
		refCode: 'scmf.ref.QualityLevelGridRefer',
		queryGridUrl: '/nccloud/scmf/refer/QualityLevelGridRefer.do',
		columnConfig: [
			{
				name: [ 'REFER-000004', 'REFER-000005' ] /* 国际化处理： 质量等级编码,质量等级名称*/,
				code: [ 'cqualitylvcode', 'cqualitylvname' ]
			}
		],
		isMultiSelectedEnabled: false,
		isHasDisabledData: false
	};

	return <Refer {...Object.assign(conf, props)} />;
}
